.container {
  max-width: 1060px;
  height: 100%;
  border-width: 0 2px 0 2px;
  border-style: solid;
  border-color: #ccc;
  margin: 0 auto;
  display: grid;
  grid-template: 1fr/1fr 3fr;
}

.container .left {
  background-color: #d8332e;
}

.container .left .sidebarwraper {
  height: 100%;
  width: 100%;
  background-image: url(./img/leftbg.jpg);
  background-position: bottom;
  background-repeat: no-repeat;
}

.container .left .sidebarwraper .sidebar {
  display: grid;
  grid-template: 150px 1fr 200px/1fr;
  width: 100%;
  height: 100%;
}

.container .left .sidebarwraper .sidebar .toplogo {
  width: 181px;
  height: 100px;
  background-image: url(./img/toplogo.jpg);
  margin: 0 auto;
}

.container .right {
  background-color: #eee;
}

.container .right .listwraper {
  width: 100%;
  height: 100%;
  display: grid;
  grid-template: 100px 1fr/1fr;
}

.container .right .listwraper .list {
  min-height: 100%;
}

.container .right .listwraper .list .inswraper {
  -webkit-box-shadow: 3px -6px 0px 0px rgba(250, 8, 89, 0.27);
          box-shadow: 3px -6px 0px 0px rgba(250, 8, 89, 0.27);
  border-style: solid;
  border-width: 4px;
  border-color: white;
  border-radius: 20px;
  background-color: #f1f1f1;
  padding: 20px;
  margin: 50px auto 0 auto;
  width: 80%;
  text-align: center;
}

.container .right .listwraper .list .inswraper H3 {
  color: red;
  font-family: 'Audiowide', cursive;
}

.container .right .listwraper .list .inswraper h4 {
  color: grey;
  font-family: 'Courgette', cursive;
}

.foot {
  font-family: 'Audiowide', cursive;
  font-size: 0.8rem;
  color: white;
}

.foot .footlogo {
  background-image: url(./img/footlogo.jpg);
  width: 50px;
  height: 41px;
  border: 2px solid grey;
  margin: 10px auto 0 auto;
}



